    max-width: 42em
}
  
.modal__form > input {
    border-radius: .3125rem;
    margin-bottom: 0.6rem;
    width: 96%;
    font-size: .875rem;
    font-family: Inter;
    padding: 10px;
    outline: none;
    caret-color: #48ccac;
    border: 1px solid #ddd;
    transition: all 0.3s ease-in;
}
  
.modal__form > input:focus {
    border: 2px solid #48ccac;
}
  
.modal__form > button {
    margin: auto;
    margin-top: 1rem;
    margin-bottom: 3rem;
    text-align: center;
    width: 100%;
    color: #fff;
    background: #1a73e8;
    padding: .625rem .75rem;
    cursor: pointer;
    border-radius: 5px;
    max-width: 130px;
    outline: none;
    border: none;
    display: block;
    text-align: center;
    transition: all 0.2s ease-in;
}

.modal__form .dishome__login{
