  background-color: #edf3f3;
}

.discover__header {
  position: relative;
  top: 0;
  left: 0.2%;
  width: 100%;
  height: 100px;
  background-color: white;
}

.discover__main {
  display: flex;
  margin-top: 8%;
}

.discover__left {
  flex: 0.64 1;
  display: flex;
  flex-direction: column;
  margin-right: 25px;
  margin-left: 13%;
}

.discover__posts {
  margin-bottom: 10%;
  margin-right: 8%;
